@use "src/viewports";

.wrapper {
  width: 100%;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: repeat(3, auto);
  justify-content: center;
  justify-items: center;
  column-gap: 40px;
  row-gap: 30px;
  color: var(--darkGrey, #31465A);

  @media screen and (max-width: viewports.$medium) {
    grid-template-columns: auto auto;
    grid-template-rows: 1fr 1fr;
    column-gap: 17px;

    &> *:nth-child(1) {
      grid-row: 1;
      grid-column: 1/2;
    }

    & > *:nth-child(2) {
      grid-row: 2;
      grid-column: 1/3;
    }

    & > *:nth-child(3) {
      grid-row: 1;
      grid-column: 2/3;
    }
  }
}

